body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #2c3e50;
  font-family: 'Arial', sans-serif;
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding: 20px;
}

.app-title {
  font-size: 2.5em;
  color: #ecf0f1;
  margin-bottom: 20px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

.video-container {
  position: relative;
  width: 720px;
  height: 560px;
  border: 3px solid #3498db;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
  overflow: hidden;
  border-radius: 10px;
}

video {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

.scanning-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 24px;
  -webkit-animation: blinker 1s linear infinite;
          animation: blinker 1s linear infinite;
}

button {
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
  background-color: #3498db;
  color: #fff;
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  margin: 10px 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

button:hover {
  background-color: #2980b9;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.result-display {
  margin-top: 20px;
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 720px; /* same width as the video container */
  background-color: #fff;
  text-align: center;
}

.result-display p {
  font-size: 20px;
  margin-bottom: 10px;
  color: #333;
}

.result-display p:last-child {
  margin-bottom: 0;
}


@-webkit-keyframes blinker {
  50% {
      opacity: 0.5;
  }
}


@keyframes blinker {
  50% {
      opacity: 0.5;
  }
}

.face-frame {
  position: absolute;
  width: 60%; 
  height: 60%; 
  top: 20%; 
  left: 20%; 
  border: 3px dashed;
  transition: border-color 0.3s ease;
}

.face-frame.red {
  border-color: #e74c3c;
}

.face-frame.green {
  border-color: #2ecc71;
}

